import triviaData from '../data/Apprentice_TandemFor400_Data.json';

const initialState = {
    currentQuestion: null,
    answerChoices: null,
    correctAnswer: null,
    incorrectAnswers: null,
    score: 0,
    questionBank: triviaData
}

const updateState = (state = initialState, action) => {
    Object.freeze(state);
    switch(action.type) {
        default:
            return state;
    }
}

export default updateState;